import React from 'react';
import './modal.css';
import { useState } from 'react';
function Modal({ children, shown, close,data }) {
const [btnname,setbtname]=useState('Unfollow')
  return shown ? (
    <div className="modal-overlay"
    
      onClick={() => {
        // close modal when outside of modal is clicked
        close();
      }}
    >
     <div className="modal"
        onClick={e => {
          // do not close modal if anything inside modal content is clicked
          e.stopPropagation();
        }}
      ><h1 className='title'> {data}</h1>
      <div className='people'>
     <img src='data:image/jpeg;base64,' className='modal-img' alt='none'></img>
      <h3 className='name-static'> sara masood</h3>
      <button className='follow' onClick={(e)=>setbtname(`${btnname ==='Follow'? "Unfollow":"Follow"}`)}>{btnname}</button>
      </div>
    
        <button className="modal-close"  onClick={close}> &#10005; {/* HTML code for a multiplication sign */}</button>
        {children}
      </div>
      
    </div>
  ) : null;
}
export default Modal