import React from 'react';
import './modal.css';

function Modal({ children, shown, close }) {
    
  return shown ? (
    <div className="shmodal-overlay"
    
      onClick={() => {
        // close modal when outside of modal is clicked
        close();
      }}
    >
     <div className="shmodal"
        onClick={e => {
          // do not close modal if anything inside modal content is clicked
          e.stopPropagation();
        }}
      > <div>
      <h4>send this profile!</h4>
    <a href='https://web.whatsapp.com/' ><img src='data:image/jpeg;base64,' className='img-path' alt=''/>  </a>
    <a href='https://www.facebook.com' ><img src='data:image/png;base64,' className='img-path' alt=''/>  </a>
     <a href='https://www.twitter.com/' ><img src='data:image/png;base64,' className='img-path' alt='twitter'/></a>
     <a href='https://www.gmail.com/' ><img src='https://toppng.com/uploads/preview/mail-logo-gmail-logo-2018-11562993752ysgnopnnhx.png' className='img-path' alt='emil'/></a>
     
      </div>
    
        <button className="modal-close"  onClick={close}>  {/* HTML code for a multiplication sign */}</button>
        {children}
      </div>
      
    </div>
  ) : null;
}
export default Modal