import React,{ useRef, useState } from 'react'
import './pin.css'
import { useSelector, useDispatch } from 'react-redux';
import { AddEvent } from '../store/slice';
const AddPin = () => {
  const inputFile = useRef(null);
  const [txt,setimg]=useState("");
  const fileBrowseHandler = (event) => {
    if (event.target.files && event.target.files[0]) {
  setimg(URL.createObjectURL(event.target.files[0]));
    }}
    const deletee =()=>
    {
      setimg("")
    }

    const boards = useSelector(state => state.board.board);
    const [desc,setdesc]=useState("")
    const [title,settitle]=useState("saved")
    const [t,sett]=useState("")
    const dispatch =  useDispatch()
    const onAdd = (title,txt,desc,t)=>{
      let c="created"
        dispatch(AddEvent.addsaved({title,txt,desc,t,c}))
        
      }
  return (
    <div className='buils_pin'>
      <div className='border-pin'> 
         <div className='contents-border'>
           <div  className='div-add-image'> 
           <div className={`${txt===""?'block':'visible'}`} >
           <img src={txt} />
           <button className='delete' onClick={deletee}>delete</button>
           </div>
             <button onClick ={ (e) => {inputFile.current.click()}}> + </button>
             <p>ADD Your Image here</p>
             <h6>recomendation:use high-qualety of .jpeg file<br/> with less tahn 20 MB</h6>
           
           </div>
           <div className='div-details'>
                 <div className='header-div-details'>
                 <select >
                 {
          boards.map((brd) => {
            return  <option onClick={(e)=>{settitle(e.target.value)}}>{brd} </option> 
         
          })}
             </select>
               
                        <button onClick={()=>{onAdd(title,txt,desc,t)}}>Save</button>
                        </div>
                        <div className='information'>
                        <input placeholder='Add Title' type='text' className='inp-tit'onChange={(e)=>{setdesc(e.target.value)}}/><br/>
                        <div className='user-details'>
                          <img className='avatar' src='data:image/jpeg;base64,' alt='' /><br/>
                          <h6>shanggmasood</h6><br/>
                        </div>
                        <input placeholder='description' type='text' className='inp-tit' onChange={(e)=>{sett(e.target.value)}}/>
                        <input
        type="file"
        id="file"
        ref={inputFile}
        onChange={fileBrowseHandler}
        style={{ display: "none" }}
      />
                        </div>
                
           </div>
         </div>
      </div>



    </div>
  )
}

export default AddPin