import React,{useState,useRef} from 'react'
import styled from 'styled-components'
const Settings=(props)=> {
    const inputFile = useRef(null);
    let [name, setName] = useState('shang M abdullah')
    let [imgg,setimg]=useState('data:image/jpeg;base64,')
    const fileBrowseHandler = (event) => {
        if (event.target.files && event.target.files[0]) {
      setimg(URL.createObjectURL(event.target.files[0]));
        }}
    
    
    return (
        <div>
            <Profile>
         <h1>Public Profile</h1>
         <p>People visiting your profile will see the following info</p>
         <Sdiv>
         <h6>photo </h6>
         <ImgP src={imgg} />
         <Btn onClick ={ (e) => {inputFile.current.click()}}> change</Btn> <br/>
         <Lbl>username</Lbl><br/>
         <In  value={name}/>
         </Sdiv>
         </Profile>
         <input
        type="file"
        id="file"
        ref={inputFile}
        onChange={fileBrowseHandler}
        style={{ display: "none" }}
      />
        </div>
    )
}

export default Settings
const Profile=styled.div
`

margin-top:100px;
text-align:center;

`
const ImgP = styled.img
`
width:100px;
height:100px;
border-radius:50px;

`
const Sdiv=styled.div
`
margin-top:30px;


`
const Btn =styled.button
`
background-color:rgb(107,107,107);
border-radius:15px;
width:50px;
height:30px;

`
const Lbl=styled.label
`
margin:10px;

`
const In=styled.input
`
width:65px
height:30px;
border-radius:16px;
border-color:#efefef;
`