import React,{useState} from 'react'
import styled from 'styled-components'
import { useNavigate } from "react-router-dom"
import './user.css'
import Fmodal from './fmodal';
import Shmodal from './shmodal'
const Usename=(props)=> {
    const [follower,setfollowers]=useState(false);
    const [folData,setfolData]=useState('');
    const [showshare,setshare]=useState(false);
    let navigate = useNavigate(); 
    const routeChange = () =>{ 
      let path = `/Settings`; 
      navigate(path);}
    return (
        <Wrapper>
            {follower && (
         <Fmodal
         shown={follower}
         data={folData}
         close={() => {
            setfollowers(false);
         }}
       />
        )}
           {showshare && (
         <Shmodal
         shown={showshare}
         close={() => {
            setshare(false);
         }}
       />
        )}
         <Container>
       <Imgg src='data:image/jpeg;base64,'
        alt='png'/>
         <h1>shangg masood</h1>
         <Userr>@shanggmasood</Userr>
         <Follow>
         <h4 onClick={(e) => {setfollowers(true); setfolData('followers'); e.preventDefault()}}>1 follower</h4>
         <span>  .  </span>
         <h4 onClick={(e) => {setfollowers(true); setfolData('following'); e.preventDefault()}}>1 following</h4>
         </Follow>
         <Btn onClick={(e) => {setshare(true);}}>
             Share
         </Btn>
    
        < button className='btn1'onClick={routeChange}>
             edit profile
         </button>
      
       
         </Container>
        </Wrapper>
    )
}

export default Usename;
const Wrapper=styled.div
`
background-color:white;
display:flex;
width:100%;
height:100%;
margin-top:90px;


`
const Container =styled.div
`
width:100%;
height:350px;
background-color:white;
margin:0 auto;
max-width:1260px;
text-align: center;
`
const Userr=styled.p
`
color:grey;
font-size:12px;
margin:15px;


`
const Follow = styled.div
`
display: flex;
flex-direction: row;
justify-content:center;
padding:15px;
right:100px;
cursor:pointer;

`

const Btn = styled.button
`
width:80px;
height:50px;
background-color: #efefef;
border-radius: 30px;
margin : 10px;
font-weight: bold;
font-size:16px;


`
const Imgg = styled.img
`
width:120px;
height:120px;
z-index:10;
border-radius: 80px;

`