import React, { useState } from 'react'
import User from './User'
import styled from 'styled-components'
import OpenAdd from './openAdd'
import './modal.css'
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom'
const Saved = (props) => {
  const [openadd,setOadd]=useState(false);
  const boards = useSelector(state => state.board.board);
  const name="all  pins"
  return (
    <div>
      <User />
      {openadd &&(
      <OpenAdd show={openadd} 
      close={() => {
        setOadd(false);}}/>
      )}
     
      <h1 className="BtnPlus"onClick={(e) => {setOadd(true);}}>
         +
      </h1>
     
       <Boards>

         {
          boards.map(brd => {
            return    <Pin_Bord  key={brd.id}>
               <Link to={`/Showpin/${brd}`} className='aa'>
            <Board></Board>
              <h2>{brd}</h2>
              </Link>
          </Pin_Bord>
          })
        }
         
       </Boards >
    </div>
  )
}

export default Saved
const Boards =styled.div
`
display: flex;
flex-direction: row;
width:100%;
height:710px;
padding-left:50px;
padding-top:20px;
position: relative;
`
const Board =styled.div
`
width:236px;
height:157px;
background-color:rgb(105,105,105);
border-radius:16px;
box-sizing: border-box;
 background-image:url(data:image/jpeg;base64,);
 
`
const Pin_Bord=styled.div
  `
  width:250px;
  height:155px;
  
  `
  const Img_Board=styled.div
  `
  width:108px;
  height:155px;;
  z-index:0;
 border-radius:16px;
border-color:white;
  background-image:url(data:image/jpeg;base64,);
 
  `
  const Btnplus=styled.button
  `
 
  
  `